
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import VTooltip from 'vux/src/components/v-chart/v-tooltip.vue'
import VLine from 'vux/src/components/v-chart/v-line.vue'
import VScale from 'vux/src/components/v-chart/v-scale.vue'
import VGuide from 'vux/src/components/v-chart/v-guide.vue'
import XHeader from 'vux/src/components/x-header/index.vue'


const data = []


function getRecord (offset) {
offset = offset || 0
return {
time: new Date().getTime() + offset * 1000,
value: Math.random() + 10
}
}

data.push(getRecord(-2))
data.push(getRecord(-1))
data.push(getRecord())

export default {
components: {
Group,
Cell,
Tabbar,
TabbarItem,
VTooltip,
VLine,
VScale,
VGuide,
XHeader
},
mounted () {
this.timer = setInterval(() => {
data.push(getRecord())
}, 2000)
},
beforeDestroy () {
clearInterval(this.timer)
},
data () {
return {
guide1: {
position (xScale, yScales) {
const xValues = xScale.values
const yValues = yScales[0].values
const xMax = xValues[xValues.length - 1]
const yMax = yValues[yValues.length - 1]
return [ xMax, yMax ]
},
html: '<div style="border-radius: 100%;border: none;width: 12px;height: 12px;background-color: rgb(24, 144, 255);transition: top 0.35s cubic-bezier(0.23, 1, 0.32, 1);"></div>'
},
guide2: {
position (xScale, yScales) {
const xValues = xScale.values
const yValues = yScales[0].values
const xMax = xValues[xValues.length - 1]
const yMax = yValues[yValues.length - 1]
return [ xMax, yMax ]
},
html: '<div style="border-radius: 100%;border: none;width: 20px;height: 20px;background-color: rgba(24, 144, 255, 0.5);transition: top 0.35s cubic-bezier(0.23, 1, 0.32, 1);"></div>'
},
data
}
},
methods: {
onItemClick (index) {
console.log('On item click ：', index)
}
}
}



