
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Divider from 'vux/src/components/divider/index.vue'


const imgList = [{
url: 'javascript:',
img: 'https://blueliuyun.github.io/pic/device_1.jpg',
title: '设备'
}, {
url: 'javascript:',
img: 'https://blueliuyun.github.io/pic/device_4.jpg',
title: 'IOT'
}]

const demoList = imgList.map((item, index) => ({
url: 'javascript:',
img: item.img,
title: `${item.title}`
}))

export default {
components: {
Group,
Cell,
Tabbar,
TabbarItem,
XHeader,
XInput,
Datetime,
XTextarea,
Swiper,
Divider
},
data () {
return {
demo02_list: demoList,
value1: '阿甘',
time1: '2018-05-01',
desc: '2018年第2季度的例行检修。设备运行正常。'
}
},
created () {
this.$nextTick(function () {
console.log('IN - created', this.desc)
})
this.Event.$on('onClickItemPanel', function (index) {
this.desc = index.desc
console.log('IN - created - Event', index.desc)
})
},
mounted () {
},
methods: {
onItemClick (index) {
console.log('On item click ：', index)
}
},
computed: {
set: function (val) {
this.desc = val
console.log('IN set desc')
},
get: function () {
return this.desc
}
},
watch: {
desc: function (val, oldVal) {
console.log('IN watch desc')
}
}
}



