
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import VChart from 'vux/src/components/v-chart/v-chart.vue'
import VTooltip from 'vux/src/components/v-chart/v-tooltip.vue'
import VLine from 'vux/src/components/v-chart/v-line.vue'
import VScale from 'vux/src/components/v-chart/v-scale.vue'
import VGuide from 'vux/src/components/v-chart/v-guide.vue'
import Panel from 'vux/src/components/panel/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'


const data = []


function getRecord (offset) {
offset = offset || 0
return {
time: new Date().getTime() + offset * 1000,
value: Math.random() + 10
}
}

data.push(getRecord(-2))
data.push(getRecord(-1))
data.push(getRecord())

export default {
name: 'Home2',
components: {
Group,
Cell,
Tabbar,
TabbarItem,
VChart,
VTooltip,
VLine,
VScale,
VGuide,
Panel,
XHeader
},
mounted () {
this.timer = setInterval(() => {
data.push(getRecord())
}, 2000)
},
beforeDestroy () {
clearInterval(this.timer)
},
data () {
return {
type: '4',
list: [{
title: '例行检修',
desc: '2018年第3季度的例行检修。',
url: '/homecell'
}, {
title: '中期检修',
desc: '2018年度中期检修。',
url: '/homecell2'
}, {
title: '例行检修',
desc: '2018年第2季度的例行检修。',
url: '/homecell3'
}],
data
}
},
methods: {
onItemClick (index) {
console.log('On item click ：', index)
},
onClickItemPanel (item) {
this.Event.$emit('onClickItemPanel', item)
}
}
}



