// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import Vuex from 'vuex'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import App from './App'
import Home from './components/HelloFromVux.vue'
import Home1 from './components/HelloFromVux1.vue'
import Home2 from './components/HelloFromVux2.vue'
import HomeCell from './components/HelloFromVuxCell.vue'
import HomeCell2 from './components/HelloFromVuxCell2.vue'
import HomeCell3 from './components/HelloFromVuxCell3.vue'

Vue.use(VueRouter)
Vue.use(Vuex)

const routes = [{
  path: '/',
  component: Home
}, {
  path: '/home1',
  component: Home1
}, {
  path: '/home2',
  component: Home2
}, {
  path: '/homecell',
  component: HomeCell
}, {
  path: '/homecell2',
  component: HomeCell2
}, {
  path: '/homecell3',
  component: HomeCell3
}]

const router = new VueRouter({
  routes
})

FastClick.attach(document.body)

Vue.config.productionTip = false

var Event = new Vue()
Vue.prototype.Event = Event

var store = new Vuex.Store({
  state: {
    desc: ' '
  }
})

/* eslint-disable no-new */
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app-box')



